import React from 'react';
import styled from 'styled-components';
import NavItem from './NavItem';
import Flamingo from './images/flamingo.png'

const HeaderStyles = styled.header`
  /* background-image: linear-gradient(#6D85B3, #3B5C99); */
  background-image: linear-gradient(#67313D, #351C21);
  height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  h1 {
    font-size: 40px;
    color: white;
    text-align: center;
    border-bottom: .5px solid white;
    padding: 20px 0;
  }

  nav {
    display: flex;
    justify-content: space-around;
    font-size: 25px;
    width: 50%;
    margin: 10px auto;
  }

  a {
    color: white;
    text-decoration: none;
  }

  a:hover {
    color: white;
  }

  @media (max-width:600px){
    height: 100px;

    h1 {
      font-size: 20px;
    }

    nav {
      font-size: 16px;
    }
  }
`;


function Header(props) {
  return(
    <HeaderStyles>
      <h1 className="text">{props.title}</h1>
      <nav>
        <NavItem href="/" text="Instrucciones"/>
        <NavItem href="/lugares" text="Lugares"/>
      </nav>
    </HeaderStyles>
  )
}

export default Header;



