import React from 'react';
import styled from 'styled-components';

const FooterStyles = styled.footer`
  background-image: linear-gradient(#67313D, #351C21);
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  h2 {
    font-size: 20px;
    color: white;
  }

  @media (max-width:600px){
    height: 50px;

    h2 {
      font-size: 15px;
      color: white;
    }
  }
`;

function Footer({ text }) {
  return (
    <FooterStyles>
      <h2>{text}</h2>
    </FooterStyles>
  )
}

export default Footer;
