import React from 'react';
import styled from 'styled-components';
import { MdStar } from "react-icons/md";
import { MdStarBorder } from "react-icons/md";

const PlaceCardStyles = styled.article`
  box-sizing: border-box;
  padding: 10px;
  margin: 20px 0;
  cursor: pointer;
  display: flex;
  box-shadow: 0px 2px 4px 0px grey;
  height: 80px;

  :hover {
    box-shadow: 0px 2px 4px 2px grey;
  }

  .info {
    display:flex;
    flex-direction:column;
    justify-content: center;
    width: 70%;
  }

  .rating {
    display:flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 30%;
  }

  .star {
    color: #e8c640;
    font-size: 20px;
  }

  h3 {
    font-size: 20px;
  }
  p {
    color: gray;
  }
`;

function PlaceCard(props) {
  const rating = [];
  for(let i=0; i<5; i++) {
    if(i <= props.rating) {
      rating.push('fill');
    } else {
      rating.push('unfill');
    }
  }
  return (
    <PlaceCardStyles onClick={props.onClick}>
      <div className="info">
        <h3>{props.name}</h3>
        <p>{props.address}</p>
      </div>
      <div className="rating">
        {rating.map((star, index) => {
          return star === 'fill' ?
            <MdStar key={props.name + '-' + star + '-' + index} className="star"/> :
            <MdStarBorder key={props.name + '-' + star + '-' + index} className="star"/> ;
        })}
      </div>
    </PlaceCardStyles>
  )
}

export default PlaceCard;
