import React, { Component } from 'react';
import { Route } from "react-router-dom";
import MapContainer from './MapContainer';
import PlacesList from './PlacesList';
import styled from 'styled-components';
import PlaceInfo from './PlaceInfo';

const PlacesContainerStyles = styled.section`
  display: flex;

  .map-container {
    width: 50%;
    height: calc(100vh - 200px);
    position: relative;
  }

  @media (max-width:600px){
    flex-direction: column;
    height: calc(100vh - 150px);

    .map-container {
      width: 100%;
      height: 40%;
    }
  }
`

class PlacesContainer extends Component {
  constructor() {
    super();
    this.state = {
      places: [],
      activePlace : {
        location: {
          lat: 19.413213,
          lng: -99.180566
        }
      }
    }
    this.changeMap = this.changeMap.bind(this);
  }

  componentDidMount() {
    fetch('http://3.210.202.227:3000/api/places')
      .then(response => response.json())
      .then(response => {
        this.setState({
          places: response.places
        })
        this.changeMap(response.places[0])
      })
      .catch(err => {
        console.log(err);
      })

  }

  changeMap(place) {
    this.setState({
      activePlace: place
    })
  }

  render() {
    return(
      <PlacesContainerStyles>
        <Route
          path="/lugares" exact
          component={()=> <PlacesList className="places-container" places={this.state.places} clickHandler={this.changeMap}/>}/>
        <Route
          path={'/lugares/' + this.state.activePlace.name}
          component={() => <PlaceInfo place={this.state.activePlace}/>}/>
        <section className="map-container">
          <MapContainer
            location={this.state.activePlace.location}
            places={this.state.places}
          />
        </section>
      </PlacesContainerStyles>
    )
  }
}

export default PlacesContainer;











