import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { MdArrowBack } from "react-icons/md";

const PlaceInfoStyles = styled.section`
  width: 50%;
  box-sizing: border-box;
  height: calc(100vh - 200px);
  padding: 40px;
  overflow-y: scroll;

  a {
    color: gray;
    text-decoration: none;
    font-weight: bold;
  }

  a:hover {
    transform: scale(1.1);
    color: black;
  }

  section {
    margin-top: 30px;
  }

  .descripcion, .list {
    margin: 20px 0;
  }

  .list ul {
    margin: 10px 40px;
  }

  @media (max-width:600px){
    width: 100%;
    height: 60%;
  }
`;

function PlaceInfo(props) {
  return(
    <PlaceInfoStyles>
      <header>
        <Link to="/lugares"><MdArrowBack/> Más lugares</Link>
      </header>
      <section>
        <h2>{props.place.name}</h2>
        <p>{props.place.address}</p>
        <p className="descripcion">Lorem Ipsum es simplemente el texto de relleno de las imprentas y archivos de texto. Lorem Ipsum ha sido el texto de relleno estándar de las industrias desde el año 1500, cuando un impresor (N. del T. persona que se dedica a la imprenta)</p>
        <div className="list">
          <h4>Platillos recomendados:</h4>
          <ul>
            {props.place.options.map(option => {
              return (
                <li>{option.name}</li>
              )
            })}
          </ul>
        </div>
        <div className="list">
          <h4>Datos curiosos:</h4>
          <ul>
            {props.place.facts.map(fact => {
              return (
                <li>{fact.text}</li>
              )
            })}
          </ul>
        </div>
      </section>
    </PlaceInfoStyles>
  )
}

export default PlaceInfo;
